<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-select @change="handleChange" v-model="value" placeholder="">
      <el-option
        v-for="item in Options"
        :key="item.id"
        :label="item.name"
        :value="item.id">
      </el-option>
    </el-select>
    <a class="el-button" :href="downloadURL" target="_blank" :download="downloadURL.split('\\').reverse()[0]"></a>
    <el-table
      :data="JoinList"
      style="width: 100%">
      <el-table-column
        prop="name"
        label=""
        width="180">
      </el-table-column>
      <el-table-column
        prop="school"
        label="">
      </el-table-column>
      <el-table-column
        prop="teamName"
        label="">
      </el-table-column>
      <el-table-column
        prop="phone"
        label="">
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
import axios from '../../services/my-axios'
export default {
  data () {
    return {
      JoinList: [],
      Options: [],
      compSelected: '',
      downloadURL: ''
    }
  },
  methods: {
    handleChange (e) {
      axios.getCompMembers(e).then(_ => {
        this.JoinList = _.data.result
        axios.getJoinExcel(e).then(_ => {
          this.downloadURL = _.data.result
        })
      })
    }
  },
  mounted () {
    axios.getAllComp().then(_ => {
      this.Options = _.data.result
    })
  }
}
</script>


