<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item> 
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div slot="header" class="clearfix">
        <span>{{title}}</span>
      </div>
      <div>
        {{detail}}
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '',
      detail: ''
    }
  },
  props: [
    'id'
  ]
}
</script>

<style>

</style>



