import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'

import MemberNew from '@/components/Member/New'
import MemberElect from '@/components/Member/Elect'
import MemberOfYear from '@/components/Member/MemberOfYear'

import GoodList from '@/components/Good/GoodList'
import GoodBorrow from '@/components/Good/Borrow'
import GoodGiveBack from '@/components/Good/GiveBack'
import GoodRecords from '@/components/Good/Records'
import GoodNewItem from '@/components/Good/NewItem'
import GoodMoneyRecord from '@/components/Good/MoneyRecord'
import BuyGood from '@/components/Good/BuyGood'
import NewMoney from '@/components/Good/NewMoney'

import CompList from '@/components/Competition/CompList'
import CompDetail from '@/components/Competition/CompDetail'
import NewComp from '@/components/Competition/NewComp'
import CompJoin from '@/components/Competition/CompJoin'

import NewTrain from '@/components/Train/NewTrain'
import TrainList from '@/components/Train/TrainList'
import TrainElect from '@/components/Train/Elect'
import TrainSat from '@/components/Train/Satisfact'
import TrainDetail from '@/components/Train/TrainDetail'
import SignUpload from '@/components/Train/SignUpload'
import SignList from '@/components/Train/SignList'

import User from '@/components/User/Index'
import Sign from '@/components/User/Sign'

import TZList from '@/components/TongZhi/TZList'
import TZDetail from '@/components/TongZhi/TZDetail'

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Sign' // 
    },
    {
      path: '/Index',
      name: 'Index',
      component: Index // 
    },
    {
      path: '/Member/year/:year',
      name: 'MemberOfYear',
      component: MemberOfYear // 
    },
    {
      path: '/Member/New',
      name: 'MemberNew',
      component: MemberNew // 
    },
    {
      path: '/Member/Elect',
      name: 'MemberElect',
      component: MemberElect // 
    },
    {
      path: '/Good/List',
      name: 'GoodList',
      component: GoodList // 
    },
    {
      path: '/Good/Borrow',
      name: 'GoodBorrow',
      component: GoodBorrow // 
    },
    {
      path: '/Good/GiveBack',
      name: 'GoodGiveBack',
      component: GoodGiveBack // 
    },
    {
      path: '/Good/Records',
      name: 'GoodRecords',
      component: GoodRecords // 
    },
    {
      path: '/Good/NewItem',
      name: 'GoodNewItem',
      component: GoodNewItem // 
    },
    {
      path: '/Good/Buy',
      name: 'BuyGood',
      component: BuyGood // 
    },
    {
      path: '/Good/MoneyRecord',
      name: 'GoodMoneyRecord',
      component: GoodMoneyRecord // 
    },
    {
      path: '/Good/MoneyRecord/NewMoney',
      name: 'NewMoney',
      component: NewMoney // 
    },
    {
      path: '/Competition',
      name: 'CompList',
      component: CompList // 
    },
    {
      path: '/Comp/Detail/:id',
      name: 'CompDetail',
      component: CompDetail,
      props: true // 
    },
    {
      path: '/Comp/CompJoin',
      name: 'CompJoin',
      component: CompJoin // 
    },
    {
      path: '/Comp/New',
      name: 'NewComp',
      component: NewComp // 
    },
    {
      path: '/Train/new',
      name: 'NewTrain',
      component: NewTrain // 
    },
    {
      path: '/Train/List',
      name: 'TrainList',
      component: TrainList // 
    },
    {
      path: '/Train/Elect',
      name: 'TrainElect',
      component: TrainElect // 
    },
    {
      path: '/Train/Sat',
      name: 'TrainSat',
      component: TrainSat // 
    },
    {
      path: '/Train/Detail/:id',
      name: 'TrainDetail',
      component: TrainDetail, // 
      props: true
    },
    {
      path: '/Train/SignUpload',
      name: 'SignUpload',
      component: SignUpload // 
    },
    {
      path: '/Train/SignList',
      name: 'SignList',
      component: SignList // 
    },
    {
      path: '/User',
      name: 'User',
      component: User // 
    },
    {
      path: '/Sign',
      name: 'Sign',
      component: Sign // 
    },
    {
      path: '/TongZhi/List',
      name: 'TZList',
      component: TZList // 
    },
    {
      path: '/TongZhi/Detail',
      name: 'TZDetail',
      component: TZDetail, // 
      props: true
    }
  ]
})



