<template>
  <el-menu
    :default-openeds="defaultOpenedMenu"
    :unique-opened="true"
    class="el-menu-demo"
    :router="route"
    background-color="#77818b"
    text-color="#fff">
    <el-submenu index="1">
      <template slot="title"></template>
      <el-submenu index="1-4">
        <template slot="title"></template>
        <el-menu-item v-for="year in routeByYear" :key="year.year" :index="year.r">{{year.year}}</el-menu-item>
      </el-submenu>
      <el-menu-item index="/Member/New"></el-menu-item>
      <el-menu-item index="/Member/Elect"></el-menu-item>
    </el-submenu>
    <el-submenu index="3">
      <template slot="title"></template>
      <el-menu-item index="/Good/List"></el-menu-item>
      <el-menu-item index="/Good/NewItem"></el-menu-item>
      <el-menu-item index="/Good/Buy"></el-menu-item>
      <el-menu-item index="/Good/Borrow"></el-menu-item>
      <el-menu-item index="/Good/GiveBack"></el-menu-item>
      <el-menu-item index="/Good/Records"></el-menu-item>
      <el-menu-item index="/Good/MoneyRecord"></el-menu-item>
    </el-submenu>
    <el-submenu index="4">
        <template slot="title"></template>
        <el-menu-item index="/Competition"></el-menu-item>
        <el-menu-item index="/Comp/New"></el-menu-item>
        <el-menu-item index="/Comp/CompJoin"></el-menu-item>
    </el-submenu>
    <el-submenu index="5">
      <template slot="title"></template>
      <el-menu-item index="/Train/New"></el-menu-item>
      <el-menu-item index="/Train/List"></el-menu-item>
      <el-menu-item index="/Train/SignUpload"></el-menu-item>
      <el-menu-item index="/Train/SignList"></el-menu-item>
      <el-menu-item index="/Train/Elect"></el-menu-item>
      <el-menu-item index="/Train/Sat"></el-menu-item>
    </el-submenu>
  </el-menu>
</template>
<script>
import axios from '../services/my-axios'
export default {
  name: 'navbar',
  data () {
    return {
      years: [],
      routeByYear: [],
      routeByMenu: true,
      activeIndex: '1',
      defaultOpenedMenu: ['1']
    }
  },
  computed: {
    route: function () {
      return this.routeByMenu
    }
  },
  mounted () {
    axios.getYears().then(_ => {
      let data = _.data
      console.log(data)
      let years = data.result
      for (let year of years) {
        console.log(year)
        this.routeByYear.push({
          year: year,
          r: `/Member/year/${year}`
        })
      }
    })
  }
}
</script>
<style scoped>
span {
  float: left;
}
.navbar > ul {
  list-style: none;
}
.navbar > ul > li {
  display: inline-block;
  width: 10%;
  text-align: center;
  vertical-align: top;
}
.navbar > ul > li > ul {
  position: relative;
}
</style>





